import React, { useState, useEffect} from 'react';
import { Link, useNavigate} from "react-router-dom";

import 'styles/page/mypage.css'

import {
    onAuthStateChanged,			
    signOut,						
    updatePassword,					
} from "firebase/auth";
import { auth } from "../firebase";


function MyPage() {

  const [user, setUser] = useState({});
  const [pwValid, setPwValid] = useState(false);

  useEffect(() => {
      onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      });
  }, [user]);

  const navigate = useNavigate();

  const logout = async () => {
      await signOut(auth); 
      alert('  .')
      navigate('/');
  };   

  const [newPassword, setNewPassword] = useState("");

  const pwdChanged = async () => { 
    try {
      if (user) {
        updatePassword(user, newPassword);
      } else {
        alert(" .");
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handlePw = (e) => {
    setNewPassword(e.target.value);
    const regex = 
      /^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+])(?!.*[^a-zA-z0-9$`~!@$!%*#^?&\\(\\)\-_=+]).{8,20}$/;
    if (regex.test(e.target.value)) {
      setPwValid(true);
    } else {
      setPwValid(false);
    }
  };

  function pwdChange(){
    document.querySelector('.mw').style.display='block';
  }

  function close(){
    alert("  .");
    document.querySelector('.mw').style.display='none'
  }
  return (
    <main id="main" className="container left_p">
       <h1 className='mypage_title'>My Porsche</h1>
       {
        user?.email == null
        ? <p className='err_login'>   .</p>
        : <p className='user'>{user?.email}  .</p>
       }
        <Link className='logout_btn' onClick={logout}></Link>
        <Link className='pwd_change_btn' onClick={pwdChange}> </Link>
       <div className='reservation_box'>
            <h2>My Reservation</h2>
            <p>      </p>
            <Link to="" className='link_reservation'> </Link>
            <Link to="" className='link_reservation'>  </Link>
            <div id="mw" className='mw'>
              <div className='bg' name="bg">
                <div className='fg'>
                  <h3> </h3>
                  <input
                    placeholder=", ,   8 "
                    onChange={handlePw} />
                  <div id="err_message" className="pwd_change_err_txt">
                    {!pwValid && newPassword.length > 0 && (
                  <div className='pwd_change_err_txt'>, ,   8  .</div>
                )}
              </div>
                  <button onClick={() => { pwdChanged(); close(); }}>  </button>
                </div>
              </div>
            </div>
       </div>
    </main>
  );
}

export default MyPage;
