import React, { useState, useEffect } from 'react';
import {useNavigate} from 'react-router-dom' 

import 'styles/responsive/rejoin.css'
import 'styles/page/join.css'

import {
  createUserWithEmailAndPassword,	
  onAuthStateChanged,			
} from "firebase/auth";
import { auth } from "../firebase";

function Join() {

  const [registerEmail, setRegisterEmail] = useState("");
  const [registerPassword, setRegisterPassword] = useState("");
  const [user, setUser] = useState({});
    
  useEffect(() => {
      onAuthStateChanged(auth, (currentUser) => {  
      setUser(currentUser);
      });
  }, [user]);

  //console.log(user);

  const checkBox = document.getElementById("chk_all");
  function checkAll(){
    if (checkBox.checked==true){ 
      for (let i=0;i<5;i++) document.getElementsByName("apply")[i].checked=true;   
    } if (checkBox.checked==false){
        for (let i=0;i<5;i++) document.getElementsByName("apply")[i].checked=false;  
      }
  }

  const [allCheck, setAllCheck] = useState(false);
  const [CheckOne, setCheckOne] = useState(false);
  const [CheckTwo, setCheckTwo] = useState(false);
  const [CheckThree, setCheckThree] = useState(false);
   
  const allBtnEvent =()=>{
    if (allCheck === false) {
      setAllCheck(true);
      setCheckOne(true);
      setCheckTwo(true);
      setCheckThree(true);
    } else {
      setAllCheck(false);
      setCheckOne(false);
      setCheckTwo(false);
      setCheckThree(false);
    } 
  };
     
  const CheckOneEvent =()=>{
    if (CheckOne === false) {
    setCheckOne(true);
  } else {
    setCheckOne(false);
    }
  };
     
  const CheckTwoEvent =()=>{
    if (CheckTwo === false) {
    setCheckTwo(true);
    } else {
    setCheckTwo(false);
    }
  };
     
  const CheckThreeEvent =()=>{
    if (CheckThree === false) {
    setCheckThree(true);
    } else {
    setCheckThree(false);
    }
  };
   
  useEffect(()=>{
    if (CheckOne === true && CheckTwo === true && CheckThree === true){
      setAllCheck(true);
    } else {
      setAllCheck(false);
    }
  }, [CheckOne,CheckTwo, CheckThree])
   

  const navigate = useNavigate();

  const register = async () => {  
    try {
      if (checkBox.checked == true) {
        const user = await createUserWithEmailAndPassword(auth, registerEmail, registerPassword);
        //console.log(user);
        navigate('/joinFinish');
      } else {
        alert('    .');
      }
    } catch (error) {
      //console.log(error.message);
      alert(' .');
    } 
  };

  const [emailValid, setEmailValid] = useState(false);
  const [pwValid, setPwValid] = useState(false);

  const handleEmail = (e) => {
    setRegisterEmail(e.target.value);
    const regex = 
      /^(([^<>()\[\].,;:\s@"]+(\.[^<>()\[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
    if (regex.test(e.target.value)) {
      setEmailValid(true);
    } else {
      setEmailValid(false)
    }
  };
  
  const handlePw = (e) => {
    setRegisterPassword(e.target.value);
    const regex = 
      /^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+])(?!.*[^a-zA-z0-9$`~!@$!%*#^?&\\(\\)\-_=+]).{8,20}$/;
    if (regex.test(e.target.value)) {
      setPwValid(true);
    } else {
      setPwValid(false)
    }
  };

  return (
    <div>
        <main id="main" className="container left_p join_container">
            <div className="join_txt">
                <h2>Porsche ID .</h2>
                <p>    Porsche ID .</p>
                <p>      .</p>
            </div>
                <legend className="hide"></legend>
                <div className="form_group txt">
                    <label htmlFor="email"> </label>
                    <input 
                        type="email" 
                        id="email" 
                        className="join_email"  
                        value={registerEmail}
                        onChange={handleEmail}/>
                    <div className='err_txt'>
                        {!emailValid && registerEmail.length > 0 && (
                        <div>  .</div>
                    )}
                    </div>
                </div>
                <div className="form_group txt">
                    <label htmlFor="pwd"></label>
                    <input 
                        type="password" 
                        id="pwd" 
                        className="join_pwd" 
                        name="pwd" 
                        value={registerPassword}
                        onChange={handlePw}/> 
                    <div id="err_message" className="err_txt">
                    {!pwValid && registerPassword.length > 0 && (
                    <div>, ,   8  .</div>
                    )}
                    </div>
                </div>
                <div className="chk_wrap">
                    <div>
                        <input type="checkbox" id="chk_all" onClick={() => {
                             checkAll() }}  checked={allCheck} onChange={allBtnEvent} />
                        <label htmlFor="chk_1"> </label>
                    </div>
                    <div>
                        <input type="checkbox" id="chk_1" name="apply" checked={CheckOne} onChange={CheckOneEvent} />
                        <label htmlFor="chk_1">   ,  .</label>
                    </div>
                    <div>
                        <input type="checkbox" id="chk_2" name="apply" checked={CheckTwo} onChange={CheckTwoEvent} />
                        <label htmlFor="chk_2">      ,  .</label>
                    </div>
                    <div>
                        <input type="checkbox" id="chk_3" name="apply" checked={CheckThree} onChange={CheckThreeEvent}  />
                        <label htmlFor="chk_3">  3     ,  .</label>
                    </div>
                    <p id="err_apply" className="err_txt"></p>
                </div>
                    <button type="submit" className="submit_btn" onClick={()=> {
                    register()
                    }}>Porsche ID  </button>
        </main>
    </div>
  );
}
export default Join;